    width: 7rem;
    height: 7rem;
  }

  .favicon-container__img {
    width: 92%;
    height: 92%;
  }

  .player-control-btns__play-arrow-wrapper {
    width: 4rem;
    height: 4rem;
  }
}

@media only screen and (min-width: 1024px) {
  .player-container {
    width: calc(100% + 6rem);
    height: calc(
      100% - (90vh * 0.04)
    );
    height: calc(
      100% - (var(--container-height) * 0.04)
    ); /* var(--container-height) * 0.07) is how much margim top this has */
    transform: translateX(-2rem)
      translateY(calc(90vh * 0.04));
    transform: translateX(-2rem)
      translateY(calc(var(--container-height) * 0.04));
    border-radius: 3rem;
    background-color: #263238;
  }

  .player-container__inner-wrapper {
    height: 100%;
    box-shadow: none;
    left: 0;
    top: 0;
    z-index: 999;
    border-radius: 0;
    border-radius: 3rem;
  }
